@charset "utf-8";

@import url('http://fonts.googleapis.com/earlyaccess/nanumgothic.css');

*{
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
  font-family: 'nanumgothic';
}

ul, li{
  list-style: none;
}

ul{
  display: flex;
  flex-wrap: wrap;
}

h1{
  text-align: center;
  padding: 10px;
}
img{
  width: 200px;
}

.product_box{
  background-color: #E6ECFA;
  margin: 10px;
  border-radius: 10px;
}
.product_list > li{
  width: 46%;
  text-overflow:ellipsis;
  white-space: nowrap;
  overflow: hidden; 
  padding: 10px;
  margin: 2%;
  background-color: white;
  border-radius: 10px;
  text-align: center;
}
.position{
  position: relative;
}
.btn{
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  color: white;
  font-weight: bold;
  width: 100%;
  margin: 10px 0;
}
.create{
  background-color: #0038C8;
}
.delete{
  background-color: #A10000;
}

.title{
  font-weight: 500;
}
.subtitle{
  text-decoration: line-through;
}
.sale{
  font-size: 24px;
  color: #A10000;
  font-weight: bold;
}
.total{
  font-size: 18px;
  font-weight: 500;
}

.inputData{
  background-color: #D9D9D9;
  padding: 10px;
  border-radius: 10px;
  margin: 10px;
}
.inputData p {
  display: flex;
  justify-content: space-between;
}
.inputData p label,
.inputData p input{
  line-height: 30px;
  margin: 5px 0;
}
.inputData p label{
  width: 20%;
  text-align: center;
  font-size: 14px;
}
.inputData p input{
  width: 80%;
  border-radius: 5px;
  border: none;
}

article{
  padding: 10px;
}
h3{
  text-align: center;
  padding: 10px 20px;
  font-weight: 500;
  font-size: 18px;
}